@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
    --primary:#3acbf7;
}
.btn{
    padding:8px 20px;
    border-radius:4px;
    outline:none;
    border:none;
    cursor:pointer;

}
.btn:hover{
    padding:8px 20px;
    transition:all 0.3s ease-out;
    background: #fff;
    color:#6568f4;
    transition:250ms;

}
.btn--primary{
    background-color: #3acbf7;
    background-color: var(--primary);
}
.btn--outline{
    background-color: transparent;
    color:#fff;
    padding: 8px 20px;
    border-radius: 4px;
    border:1px solid #3acbf7;
    border:1px solid var(--primary);
    transition:all 0.3s ease-out;
}
.btn--medium{
    padding:8px 20px;
    border-radius:4px;
    font-size:18px;
    color:#fff;
}
.btn--large{
    padding:12px 26px;
    border-radius:4px;
    font-size:20px;
    color:#fff;

}
.NavBarItems{
    position: fixed;
    background: #56c8e7;
    height:80px;
    width: 100%;
    display:flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
   
}
.navbar-logo{
    color:#fff;
    justify-self: start;
    margin-left: 20px;
    cursor:pointer;

}
.fa-react{
    margin-left: 0.5rem;
    font-size: 1.6rem;
}
.nav-menu{
    display:grid;
    grid-template-columns: repeat(5,auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
    margin-right: 2rem
}
.nav-links{
    color: white;
    text-decoration: none;
    padding:0.5rem 1rem;
}
.nav-links:hover{
    background-color:#6d76f7;
    border-radius: 4px;
    transition: all 0.2s ease-out; 
}
.fa-bars{
    color: white
}
.nav-links-mobile{
    display: none;
}
.menu-icon{
    display:none;
}
@media screen and (max-width:960px){
    .NavBarItems{
        position:relative;
    }
    .nav-menu{
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 500px;
        position: absolute;
        top:80px;
        left: -800px;
        opacity: 1;
        transition: all 0.5s ease;

    }
    .nav-menu.active{
        background: #6668f4;
        left:0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }
    .nav-links{
        text-align:center;
        padding:2rem;
        width: 100%;
        display:table;

    }
    .nav-links:hover{
        background-color: #7577fa;
        border-radius: 0;

    }
    .navbar-logo{
        position: absolute;
        top:0;
        left:0;
        transform: translate(25% 50%);
    }
    .menu-icon{
        display: block;
        position: absolute;
        top:0;
        right: 0;
        transform: translate(-100%,60%);
        font-size: i.8rem;
        cursor: pointer;
    }
    .fa-times{
        color:#fff;
        font-size:2rem;
    }
    .nav-links-mobile{
        display: block;
        text-align: center;
        padding: 1.5rem;
        margin:2rem auto;
        border-radius: 4px;
        width:80%;
        background: #4ad9ef;
        text-decoration: none;
        color:#fff;
        font-size: 1.5rem;



    }
    .nav-links-mobile:hover{
        background: #fff;
        color:#6568f4;
        transition:250ms;

    }
    Button{
        display:none;
    }
}

